import React from 'react';
import "./FirstSection.css";
import "./Carousel.css";
import {Carousel} from 'react-carousel-minimal';
import {CarouselData} from './CarouselData';


const FirstSection = () => {
    const captionStyle = {
        fontSize: '2em',
        fontWeight: 'bold',
      }

    return (
        <div id="#1" className="firstSection">
            <Carousel 
                data={CarouselData}
                time={4000}
                width="100%"
                height="100vh"
                captionStyle={captionStyle}
                radius="0"
                slideNumber={false}
                captionPosition="center"
                automatic={true}
                dots={true}
                pauseIconColor="white"
                pauseIconSize="5rem"
                slideBackgroundColor="darkgrey"
                slideImageFit="cover"
                thumbnails={false}
                style={{
                  textAlign: "center",
                  maxWidth: "100%",
                  maxHeight: "auto",
                  borderRadius: 0,
                }}
            />

        </div>
    );
};

export default FirstSection;